@media only screen and (max-width: 900px) and (min-width: 750px)  {
  .container{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    padding-left: 10rem;
    padding-right: 33rem,
  }
}

@media only screen and (max-width:740px) and (min-width:320px) {
  .container{
    display: grid;
    grid-template-columns: repeat(1,1fr);
    padding-left: 2rem;
  }
}
