.live_Demo,.repo {
width: 50%;
height: 50px;
font-size: small;
border-left: 5px solid rgb(235,238,238);
border-right: 5px solid rgb(235,238,238);
transform-origin: 0 100%;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    transform: translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
}



.live_Demo:hover, .but,.repo:hover {
    animation-name: wobble-top;
    animation-duration: 1s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: 1;
}

@keyframes wobble-top {
    16.65% {
        transform: skew(-12deg);
   }
    33.3% {
        transform: skew(10deg);
   }
    49.95% {
        transform: skew(-6deg);
   }
    66.6% {
        transform: skew(4deg);
   }
    83.25% {
        transform: skew(-2deg);
   }
    100% {
        transform: skew(0);
   }
}

.repo {
    width: 50%;
    height: 50px;
    font-size: small;
    border-left: 5px solid rgb(235,238,238);
    border-right: 5px solid rgb(235,238,238);
}

